import React, { Component } from 'react'

export default class WeatherDataComponent extends Component {

    shouldComponentUpdate(nextProps,nextState){
        // controling 
        if(nextProps.city===this.props.city && nextProps.temp===this.props.temp){
            return false
        }
        return true
    }

    render() {
        return (
            <div>
                <p>Feels like {this.props.feelsLike}</p>
                <img src={`http://openweathermap.org/img/wn/${this.props.icon}@4x.png

`} alt=''/>
                <div>
                <h1>{this.props.temp}</h1>
                </div>
                <div>
                    <p>{this.props.humidity}</p>
                    <p>{this.props.pressure}</p>
                    <p>{this.props.maxTemp}</p>
                    <p>{this.props.minTemp}</p>
                </div>
            </div>
        )
    }
}
